"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { createBrowserClient } from "@supabase/ssr";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import TenantHeader from "@/components/TenantHeader";
import {
  Download,
  FileText,
  Wrench,
  CreditCard,
  MapPin,
  Calendar,
  CheckCircle,
  AlertCircle,
  Home,
  DollarSign,
} from "lucide-react";
import Link from "next/link";

interface TenantSession {
  id: string;
  full_name: string;
  email: string;
  phone: string;
  lease_start?: string;
  lease_end?: string;
  payment_status?: string;
}

interface Apartment {
  id: string;
  name: string;
  unit_number: string;
  monthly_rent: number;
  bedrooms: number;
  bathrooms: number;
  size: number;
  description?: string;
}

interface Booking {
  id: string;
  tenant_id: string;
  apartment_id: string;
  check_in_date: string;
  check_out_date: string;
  status: string;
}

export default function TenantDashboard() {
  const router = useRouter();
  const [tenant, setTenant] = useState<TenantSession | null>(null);
  const [booking, setBooking] = useState<Booking | null>(null);
  const [apartment, setApartment] = useState<Apartment | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const supabase = createBrowserClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
  );

  useEffect(() => {
    const fetchTenantData = async () => {
      try {
        const tenantData = localStorage.getItem("tenant_session");
        if (!tenantData) {
          router.push("/tenant/login");
          return;
        }

        const parsedTenant: TenantSession = JSON.parse(tenantData);
        setTenant(parsedTenant);

        // Fetch booking details
        const { data: bookingData, error: bookingError } = await supabase
          .from("bookings")
          .select("*")
          .eq("tenant_id", parsedTenant.id)
          .single();

        if (bookingError && bookingError.code !== "PGRST116") {
          console.error("Error fetching booking:", bookingError);
        }

        if (bookingData) {
          setBooking(bookingData);

          // Fetch apartment details
          const { data: apartmentData, error: apartmentError } = await supabase
            .from("apartments")
            .select("*")
            .eq("id", bookingData.apartment_id)
            .single();

          if (apartmentError) {
            console.error("Error fetching apartment:", apartmentError);
          }

          if (apartmentData) {
            setApartment(apartmentData);
          }
        }
      } catch (err) {
        console.error("Error in fetchTenantData:", err);
        setError("Failed to load dashboard data");
      } finally {
        setLoading(false);
      }
    };

    fetchTenantData();
  }, [router, supabase]);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading your dashboard...</p>
        </div>
      </div>
    );
  }

  if (!tenant) {
    return null;
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center max-w-md">
          <AlertCircle className="h-16 w-16 text-red-600 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-gray-800 mb-2">Error</h2>
          <p className="text-gray-600 mb-6">{error}</p>
          <Button onClick={() => window.location.reload()}>Try Again</Button>
        </div>
      </div>
    );
  }

  const getPaymentStatusColor = (status: string) => {
    switch (status?.toLowerCase()) {
      case "paid":
        return "bg-green-100 text-green-800 border-green-300";
      case "pending":
        return "bg-yellow-100 text-yellow-800 border-yellow-300";
      case "overdue":
        return "bg-red-100 text-red-800 border-red-300";
      default:
        return "bg-gray-100 text-gray-800 border-gray-300";
    }
  };

  const getPaymentStatusIcon = (status: string) => {
    switch (status?.toLowerCase()) {
      case "paid":
        return <CheckCircle className="h-5 w-5" />;
      case "overdue":
        return <AlertCircle className="h-5 w-5" />;
      default:
        return <CreditCard className="h-5 w-5" />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <TenantHeader tenant={tenant} />

      <main className="container mx-auto px-4 py-8">
        {/* Welcome Section */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">
            Welcome back, {tenant.full_name}! ðŸ‘‹
          </h1>
          <p className="text-gray-600">Manage your apartment and stay updated</p>
        </div>

        {/* Quick Stats */}
        {apartment && (
          <div className="grid md:grid-cols-4 gap-4 mb-8">
            <Card>
              <CardContent className="pt-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Monthly Rent</p>
                    <p className="text-2xl font-bold text-blue-600">
                      ${apartment.monthly_rent}
                    </p>
                  </div>
                  <DollarSign className="h-10 w-10 text-blue-200" />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="pt-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Bedrooms</p>
                    <p className="text-2xl font-bold text-purple-600">
                      {apartment.bedrooms}
                    </p>
                  </div>
                  <Home className="h-10 w-10 text-purple-200" />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="pt-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Unit Number</p>
                    <p className="text-2xl font-bold text-indigo-600">
                      {apartment.unit_number}
                    </p>
                  </div>
                  <MapPin className="h-10 w-10 text-indigo-200" />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="pt-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Payment Status</p>
                    <p className="text-2xl font-bold capitalize">
                      {tenant.payment_status || "N/A"}
                    </p>
                  </div>
                  <div className={`h-10 w-10 rounded-full flex items-center justify-center ${getPaymentStatusColor(tenant.payment_status || "")}`}>
                    {getPaymentStatusIcon(tenant.payment_status || "")}
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        )}

        {/* Main Content Grid */}
        <div className="grid lg:grid-cols-3 gap-8">
          {/* Left Column - Information */}
          <div className="lg:col-span-2 space-y-6">
            {/* Apartment Details */}
            {apartment ? (
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Home className="h-5 w-5 text-blue-600" />
                    Your Apartment
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="grid md:grid-cols-2 gap-6">
                    <div>
                      <p className="text-sm font-medium text-gray-500 uppercase">
                        Apartment Name
                      </p>
                      <p className="text-lg font-semibold text-gray-900 mt-1">
                        {apartment.name}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-gray-500 uppercase">
                        Unit Number
                      </p>
                      <p className="text-lg font-semibold text-gray-900 mt-1">
                        {apartment.unit_number}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-gray-500 uppercase">
                        Size
                      </p>
                      <p className="text-lg font-semibold text-gray-900 mt-1">
                        {apartment.size} sqft
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-gray-500 uppercase">
                        Bathrooms
                      </p>
                      <p className="text-lg font-semibold text-gray-900 mt-1">
                        {apartment.bathrooms}
                      </p>
                    </div>
                  </div>
                  {apartment.description && (
                    <div className="pt-4 border-t">
                      <p className="text-sm font-medium text-gray-500 uppercase">
                        Description
                      </p>
                      <p className="text-gray-700 mt-2">{apartment.description}</p>
                    </div>
                  )}
                </CardContent>
              </Card>
            ) : (
              <Card>
                <CardContent className="pt-6">
                  <p className="text-gray-600">No apartment assigned yet</p>
                </CardContent>
              </Card>
            )}

            {/* Lease Information */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Calendar className="h-5 w-5 text-green-600" />
                  Lease Information
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <p className="text-sm font-medium text-gray-500 uppercase">
                      Lease Start Date
                    </p>
                    <p className="text-lg font-semibold text-gray-900 mt-1">
                      {tenant.lease_start
                        ? new Date(tenant.lease_start).toLocaleDateString("en-US", {
                            year: "numeric",
                            month: "long",
                            day: "numeric",
                          })
                        : "N/A"}
                    </p>
                  </div>
                  <div>
                    <p className="text-sm font-medium text-gray-500 uppercase">
                      Lease End Date
                    </p>
                    <p className="text-lg font-semibold text-gray-900 mt-1">
                      {tenant.lease_end
                        ? new Date(tenant.lease_end).toLocaleDateString("en-US", {
                            year: "numeric",
                            month: "long",
                            day: "numeric",
                          })
                        : "N/A"}
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right Column - Quick Actions */}
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Quick Actions</CardTitle>
              </CardHeader>
              <CardContent className="space-y-3">
                <Button
                  asChild
                  className="w-full justify-start gap-2 bg-blue-600 hover:bg-blue-700"
                >
                  <Link href="/tenant/profile">
                    <FileText className="h-4 w-4" />
                    View Profile
                  </Link>
                </Button>

                <Button asChild variant="outline" className="w-full justify-start gap-2">
                  <Link href="/tenant/payment-history">
                    <CreditCard className="h-4 w-4" />
                    Payment History
                  </Link>
                </Button>

                <Button asChild variant="outline" className="w-full justify-start gap-2">
                  <Link href="/tenant/maintenance">
                    <Wrench className="h-4 w-4" />
                    Request Maintenance
                  </Link>
                </Button>

                <Button asChild variant="outline" className="w-full justify-start gap-2">
                  <Link href="/tenant/profile">
                    <Download className="h-4 w-4" />
                    Download Contract
                  </Link>
                </Button>
              </CardContent>
            </Card>

            {/* Contact Information */}
            <Card>
              <CardHeader>
                <CardTitle>Contact Information</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-gray-600">Full Name</p>
                  <p className="font-semibold text-gray-900">{tenant.full_name}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Email</p>
                  <p className="font-semibold text-gray-900 break-all">{tenant.email}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Phone</p>
                  <p className="font-semibold text-gray-900">{tenant.phone || "N/A"}</p>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  );
}